export default {
	data() {
		return {
			user: '',
			pswd: '',
			email: ''
		}
	},
	methods: {
		inputChange (e, val) {
			let _this = this;
			if (val == '账号') {
				_this.user = e.detail.value;
				if (!this.user) {
					uni.showToast({
						icon: 'none',
						title: '账号不能为空'
					});
					return false;
				}
			} else if (val == '密码') {
				_this.pswd = e.detail.value;
				if (!this.pswd) {
					uni.showToast({
						icon: 'none',
						title: '密码不能为空'
					});
					return false;
				} 
			} else if (val == '邮箱') {
				_this.email = e.detail.value;
				if (!this.email) {
					uni.showToast({
						icon: 'none',
						title: '邮箱不能为空'
					});
					return false;
				} 
			}
		},
		handleSubmit () {
			if (!!this.user && !!this.pswd && !!this.email) {
				uni.setStorageSync('user', this.user);
				uni.setStorageSync('pswd', this.pswd);
				uni.setStorageSync('email', this.email);
				uni.showToast({
					icon: 'none',
					title: '注册成功'
				});
				uni.navigateTo({
					url: '../login/login'
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请输入信息'
				});
				return false;
			}
		}
	}
}
